<template>
  <div style="height:100%">
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <mt-tabbar :fixed="true">
      <mt-tab-item @click.native="changeTo('home')">
        <img slot="icon" :src="tab=='home'?'/static/img/tab_home.png':'/static/img/tab_home_normal.png'">
        首页
      </mt-tab-item>
      <mt-tab-item @click.native="changeTo('feidian')">
        <img slot="icon" :src="tab=='feidian'?'/static/img/tab_feidian_press.png':'/static/img/tab_feidian.png'">
        沸点
      </mt-tab-item>
      <mt-tab-item @click.native="changeTo('explore')">
        <img slot="icon" :src="tab=='explore'?'/static/img/tab_explore.png':'/static/img/tab_explore_normal.png'">
        发现
      </mt-tab-item>
      <mt-tab-item @click.native="changeTo('profile')">
        <img slot="icon" :src="tab=='profile'?'/static/img/tab_profile.png':'/static/img/tab_profile_normal.png'">
        我
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>
<script>
import { mapState } from "vuex";

export default {
  name: "index",
  data() {
    return {};
  },
  computed: {
    ...mapState(["tab"])
  },
  methods: {
    changeTo(val) {
      this.$router.push("/index/" + val);
    }
  }
};
</script>



